.navbar {
    display: flex;
    justify-content:space-between;
    align-items: center;
    position: fixed;
    bottom: 0px;
    z-index: 100;
    background-color: #011826;
    height:50px;
    width:100%;
}
.navbar a { color: #F2F2F2; text-decoration: none; }
.navbar a:hover { color: #91CDF2; }
.navbar a:active { color: #3B8DBF;}
.nav { margin: 0px 15px 3px; }
.navicon { margin: 0px 0px 0px 10px; }
.navitem  { margin: 0px 20px 0px 0px; }
.nav-active { border-bottom: 3px solid #F2F2F2; }
@media screen and (max-width: 430px) {
    .nav { margin: 0px 10px 0px;}
    .navicon { margin: 0px 0px 0px 5px; }
    .navitem { margin: 0px 10px 0px 0px; }
}
@media screen and (max-width: 345px) {
    .navicon { margin: 0px 0px 0px 2px; }
    .navitem { margin: 0px 5px 0px 0px; }
    .navtext { font-size: 11px; }
}
@media screen and (max-width: 280px) {
    .navicon { margin: 0px 0px 0px 2px; }
    .navitem { margin: 0px 5px 0px 0px; }
    .navtext { display: none; }
}
.logo {
    position: fixed;
    z-index: 100;
    right: 10px;
    top: 10px;
    color: #023859;
    opacity: 0.3;
    height: 20px;
    width: 20px;
}
h1 {
    margin: 0px;
    padding: 0px;
    
}

.container {
    margin: 15px 15px 50px;
    padding-bottom: 100px;
}

.box {
    position: relative;
    z-index: 0;
    aspect-ratio: 16 / 9;
    width: 90%;
    margin: 10px;
    border-radius: 10px;
    background-color: #fff;
    box-shadow: 5px 5px 10px -5px rgb(13,13,13,0.1);
    overflow: hidden;
}
.item {
    margin: 5px;
}
.box .slide-left, .box .slide-right {
    position: absolute;
    z-index: 10;
    width: 80px;
    height: 100%;
    opacity: 0.1;
}
.slide-right { right: 0; }
.slide-left { left: 0; }
.slide-left:hover, .slide-left:active { background-color: #590A0A; }
.slide-right:hover,  .slide-right:active { background-color: #023859; }
